<template>
    <div>
        <h1>dcweb配置页面</h1>
        <p>
            <a href="/dcweb">进行dcweb</a>
        </p>
        <hr>
        <h3>自定义模块开关</h3>
        <el-form :inline="true" :model="form" class="demo-form-inline">
            <el-form-item :label="item.label" v-for="item in customModule.form" :key="item.key">
                <el-switch v-model="form[item.key]" placeholder="审批人"></el-switch>
            </el-form-item>
        </el-form>
        <el-button type="primary" size="mini" @click="saveCustomModule">保存</el-button>
    </div>
</template>
<script>
import customModule from '@/assets/custom_module.js'
export default {
  data () {
    return {
      customModule,
      form: {}
    }
  },
  created () {
    this.getCustomModule()
  },
  methods: {
    getCustomModule () {
      this.axios.get(this.customModule.getUrl)
        .then(res => {
          this.form = res.data
        })
    },
    saveCustomModule () {
      this.axios.post(this.customModule.saveUrl, this.form)
        .then(res => {
          this.$message({
            type: 'success',
            message: '保存成功'
          })
        })
    }
  }
}
</script>



